import React from 'react';
import {
    HashRouter,
    Route,
    Switch,
} from 'react-router-dom'
import RrivateRoute from './PrivateRoute'
import login from './page/login';
import Home from './page/home';
import reduxPage from './page/reduxPage';
import ParentRouter from './page/ParentRouter';


const RootRouter = () => (
    <HashRouter >
        <div className="App">
            <Switch>
                <RrivateRoute exact path="/" component={Home} />
                <Route exact path="/login" component={login} />
                <RrivateRoute exact path="/reduxPage" component={reduxPage} />
                <RrivateRoute  path="/ParentRouter" component={ParentRouter} />
            </Switch>
        </div>
    </HashRouter>
)

export default RootRouter;